.main_main {
    grid-area: main;
    display: grid;
    grid-template-areas: 
        'about'
        'searcher'
        'blog';
    
}

/* text about */

.content_main {
    grid-area: about;
    display: grid;
    gap: 1.5rem;
    grid-template-columns: minmax(15px, 0.5fr) 1fr 1fr minmax(15px, 0.5fr);
}

.content-title_main {
    place-self: center;
    grid-column: 2 / span 1;
    font-size: 44px;
    font-family: var(--heder-logo-text-font);
    color: var(--clr-logo-text);
}

.col-1-text_main {
    font-family: var(--heder-logo-text-font);
    color: var(--clr-logo-text);
}

.col-1_main {
    grid-column: 2 / span 1;
    font-size: 20px;
    font-family: var(--main-text-font);
    color: var(--clr-text);
}

.col-2_main {
    grid-column: 2 / span 1;
    font-size: 20px;
    font-family: var(--main-text-font);
    color: var(--clr-text);
} 

.content-img_main {
    grid-column: 3 / span 1;
    grid-row: 1 / span 3;
}

@media screen and (max-width: 1100px) {
    .content-img_main{
        width: 300px;
        grid-column: 3 / span 1;
        grid-row: 1 / span 3;
        place-self: center;
    }
}

.color-main-text_main {
    color: var(--clr-logo-text);
    font-family: var(--heder-logo-text-font);
}


/* Search */

.search-bar_main {
    margin-top: 10rem;
    display: grid;
    grid-auto-columns: minmax(15px, 1fr) 1fr 1fr minmax(15px, 1fr);
    grid-template-rows: 120px;
}

.search-title_main {
    font-family: var(--main-text-font);
    font-size: 44px;
    color: var(--clr-text);
    grid-column: 2 / span 2;
    grid-row: 1;
    place-self: center;
}

.info_main {
    place-self: center;
    font-family: var(--main-text-font);
    font-size: 22px;
    color: var(--clr-text);
    grid-column: 2 / span 1;
    grid-row: 2;
}


.placeholder_main {
    grid-column: 3 / span 1;
    grid-row: 2 / span 1;
    place-self: start start;
    margin: 1rem;
    margin-top: 3.25rem;
    font-family: var(--main-text-font);
    font-weight: bold;
    color: #65657b;
    left: 55rem;
    line-height: 14px;
    pointer-events: none;
    transform-origin: 0 50%;
    transition: transform 250ms, color 250ms;
}

.input_main {
    grid-column: 3 / span 1;
    grid-row: 2;
    width: 100%;
    margin-bottom: 1rem;
    margin-top: 2.5rem;
    height: 2.5rem;
    place-self: center;
    background-color: #ffffff;
    border-radius: 3px;
    border: 1px solid var(--clr-text);
    font-size: 18px;
    outline: 0;
}

.input_main:focus~.placeholder_main,
.input_main:not(:placeholder-shown)~.placeholder_main {
    transform: translateY(-35px);
}

.input_main:not(:placeholder-shown)~.placeholder_main {
    color: #808097;
}

.input_main:focus~.placeholder_main {
    color: var(--clr-logo-text);
}

.search_main {
    margin-top: 2.5rem;
    grid-column: 4 / span 1;
    grid-row: 2;
    margin-left: 1rem;
    place-self: start start;
    height: 2.5rem;
    background-color: white;
    border-radius: 3px;
    border: 1px solid var(--clr-text);
    color: var(--clr-text);
    cursor: pointer;
    font-size: 18px;
    text-align: center;
}

.search_main:hover {
    background-color: var(--clr-text);
    color: white;
}


.btn_main {
	grid-column: 3 / span 1;
	grid-row: 3;
	display: flex;
	flex-direction: column;
	width: 100%;
}

.btn_main > * 
{   
    display: none;
	margin: 1rem 0 1rem 0;
	font-family: var(--heder-logo-text-font);
	font-size: 20px;
	color: var(--clr-logo-text);
	background-color: rgba(33, 83, 68, 0);
	border: 0px;
	cursor: pointer;
	height: 2rem;
	text-align: start;
}

.blog_main {
    margin: 30rem;
}