.logo {
    display: none;
}

.title a {
    font-family: var(--heder-logo-text-font);
    font-size: var(--header-logo-text-size);
    color: var(--clr-logo-text);
    text-decoration: none;
}

nav {
    position: absolute;
    text-align: left;
    top: 100%;
    left: 0;
    background: var(--clr-background);
    height: 0;
    overflow: hidden;
}

.nav--open {
    height: auto;
}

nav ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

nav li {
    margin-bottom: 1em;
    margin-left: 1em;
}

nav a {
    color: var(--clr-text);
    font-family: 'Rubik';
    font-weight: 10;
    text-decoration: none;
    font-size: 19px;
    text-transform: uppercase;
}

nav a:hover {
    color: rgb(233, 131, 36);
}

.nav-toggle {
    padding: .5rem;
    position: absolute;
    top: 2em;
    left: 1.3em;
    cursor: pointer;
}

.nav-toggle-lable,
.nav-toggle-lable::before,
.nav-toggle-lable::after {
    content: '';
    display: block;
    background: var(--clr-text);
    height: 3px;
    width: 2em;
    border-radius: 3px;
    transition: all ease-in-out 500ms;
}

.nav-toggle-lable::before {
    transform: translateY(-7px);
}

.nav-toggle-lable::after {
    transform: translateY(4px);
}

.open .nav-toggle-lable {
    transform: rotate(45deg);
}

.open .nav-toggle-lable::before {
    opacity: 0;
}

.open .nav-toggle-lable::after {
    transform: translateY(-3px) rotate(90deg);
}

@media screen and (min-width: 1100px) {
    .nav-toggle-lable {
        display: none;
    }

    .header {
        display: grid;
        grid-template-columns: 1fr 80px auto minmax(800px, 3fr) 1fr;
    }

    .logo {
        display: block;
        grid-column: 2 / span 1;
        grid-row: 1;
        place-self: center;
    }

    .title {
        grid-column: 3 / span 1;
    }

    nav {
        all: unset;
        grid-column: 4 / span 1;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    nav li {
        margin-left: 3em;
        margin-bottom: 0;
    }

    nav ul {
        display: flex;
        justify-content: flex-end;
    }

    nav a {
        opacity: 1;
        position: relative;
    }

    nav a::before {
        content: '';
        display: block;
        height: 3px;
        background: rgb(233, 131, 36);
        position: absolute;
        top: -.75em;
        left: 0;
        right: 0;
        transform: scale(0, 1);
        transform-origin: left;
        transition: transform ease-in-out 250ms;
    }

    nav a:hover::before {
        transform: scale(1, 1);
    }
    
    nav li.active a::before{
        content: '';
        display: block;
        height: -10px;
        background: rgb(233, 131, 36);
        position: absolute;
        top: -.75em;
        left: 0;
        right: 0;
        transform: scale(1, 1);

    }

    nav li.active a{
        color: rgb(233, 131, 36);
    }
}
